/* 기존 .container 스타일 외에, 배너 전용 스타일 */
body {
  background-color: #ebdbdb;
}

.banner-movie {
  position: relative;
  margin-top: 100px;
  width: 80%;
  height: 300px;
  border-radius: 40px;
  /* 예시: 배경 이미지 오버레이 */
  background: linear-gradient(to right, #f2dde3, #d8d5eb);
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 60px;
}

.banner-movie img {
  max-width: 80%; /* 부모 너비를 초과하지 않도록 제한 */
  height: auto; /* 가로 비율에 맞춰 세로 자동 조정 */
  display: block; /* 이미지의 하단 여백 제거 (인라인 요소 기본 성격 제거) */
  object-fit: cover;
}

/* 배너 내부 텍스트 스타일 */
.banner-movie h2 {
  padding-left: 50px;
  color: rgb(28, 26, 26);
  font-size: 1.8rem;
}

/* 전체 컨테이너: 왼쪽(타이틀+이미지)과 오른쪽(나머지 정보) 가로 배치 */
.container {
  display: flex; /* 가로 배치 */
  align-items: flex-start; /* 상단 정렬 */
  gap: 20px; /* 왼쪽과 오른쪽 간격 */
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #e5e5e5;
  margin-bottom: 20px;
}

/* 왼쪽: 타이틀과 이미지를 세로로 묶어서 정렬 */
.left-content {
  display: flex;
  flex-direction: column; /* 세로로 정렬 */
  align-items: center; /* 가운데 정렬 */
  gap: 10px; /* 타이틀과 이미지 사이 간격 */
  word-wrap: break-word; /* 긴 단어도 줄바꿈 */
  max-width: 150px;
}

/* 타이틀 스타일 */
.left-content h2 {
  margin: 0;
  font-size: 1.5rem;
  text-align: center;
  color: #4b0082; /* 링크 색상 */
}

/* 이미지 스타일 */
.left-content img {
  max-width: 150px;
  border-radius: 8px;
  object-fit: cover;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* 오른쪽: summary, genres 등 */
.movie-grid {
  display: flex;
  flex-direction: column; /* 세로로 나열 */
  justify-content: flex-start; /* 상단 정렬 */
  flex-grow: 1; /* 남은 공간 차지 */
}
